/**
 * Site header
 */
 .site-header {
    border-top: 5px solid $grey-color-dark;
    border-bottom: 1px solid $grey-color-light;
    min-height: 56px;
    background: #2D3339;
    position: relative;
    transition: border-color 0.3s ease;

    [data-theme="dark"] & {
        border-bottom-color: $border-color-dark;
        background: darken(#2D3339, 5%);
    }
}

.site-title {
    font-size: 26px;
    font-weight: 300;
    line-height: 56px;
    letter-spacing: -1px;
    margin-bottom: 0;
    float: left;
    transition: color 0.3s ease;

    &,
    &:visited {
        color: #ccc;
    }

    [data-theme="dark"] & {
        &,
        &:visited {
            color: lighten(#ccc, 10%);
        }
    }
}

.site-nav {
    float: right;
    line-height: 56px;

    .menu-icon {
        display: none;
    }

    .page-link {
        color: #fff;
        line-height: $base-line-height;

        // Gaps between nav items, but not on the last one
        &:not(:last-child) {
            margin-right: 20px;
        }
    }

    @include media-query($on-palm) {
        position: absolute;
        top: 9px;
        right: $spacing-unit / 2;
        background-color: #2D3339;        
        border: 1px solid $grey-color-light;
        border-radius: 5px;
        text-align: right;
        transition: background-color 0.3s ease, border-color 0.3s ease;

        [data-theme="dark"] & {
            background-color: darken(#2D3339, 5%);
            border-color: $border-color-dark;
        }

        .menu-icon {
            display: block;
            float: right;
            width: 36px;
            height: 26px;
            line-height: 0;
            padding-top: 10px;
            text-align: center;

            > svg {
                width: 18px;
                height: 15px;

                path {
                    fill: $grey-color-dark;
                    transition: fill 0.3s ease;

                    [data-theme="dark"] & {
                        fill: $grey-color-light;
                    }
                }
            }
        }

        .trigger {
            clear: both;
            display: none;
        }

        &:hover .trigger {
            display: block;
            padding-bottom: 5px;
        }

        .page-link {
            display: block;
            padding: 5px 10px;
            margin-left: 20px;

            &:not(:last-child) {
                margin-right: 0;
            }
        }
    }
}

/**
 * Site footer
 */
.site-footer {
    border-top: 1px solid $grey-color-light;
    padding: $spacing-unit 0;
    transition: border-color 0.3s ease;

    [data-theme="dark"] & {
        border-top-color: $border-color-dark;
    }
}

.footer-heading {
    font-size: 18px;
    margin-bottom: $spacing-unit / 2;

    [data-theme="dark"] & {
        color: $text-color-dark;
    }
}

.contact-list,
.social-media-list {
    list-style: none;
    margin-left: 0;

    [data-theme="dark"] & {
        color: $text-color-dark;
    }
}

.footer-col-wrapper {
    font-size: 15px;
    color: $grey-color;
    margin-left: -$spacing-unit / 2;
    @extend %clearfix;
    transition: color 0.3s ease;

    [data-theme="dark"] & {
        color: $grey-color-dark-theme;
    }
}

.footer-col {
    float: left;
    margin-bottom: $spacing-unit / 2;
    padding-left: $spacing-unit / 2;
}

.footer-col-1 {
    width: -webkit-calc(35% - (#{$spacing-unit} / 2));
    width:         calc(35% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
    width: -webkit-calc(20% - (#{$spacing-unit} / 2));
    width:         calc(20% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
    width: -webkit-calc(45% - (#{$spacing-unit} / 2));
    width:         calc(45% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {
    .footer-col-1,
    .footer-col-2 {
        width: -webkit-calc(50% - (#{$spacing-unit} / 2));
        width:         calc(50% - (#{$spacing-unit} / 2));
    }

    .footer-col-3 {
        width: -webkit-calc(100% - (#{$spacing-unit} / 2));
        width:         calc(100% - (#{$spacing-unit} / 2));
    }
}

@include media-query($on-palm) {
    .footer-col {
        float: none;
        width: -webkit-calc(100% - (#{$spacing-unit} / 2));
        width:         calc(100% - (#{$spacing-unit} / 2));
    }
}

/**
 * Page content
 */
.page-content {
    padding: $spacing-unit 0;
}

.page-heading {
    font-size: 20px;

    [data-theme="dark"] & {
        color: $text-color-dark;
    }
}

.post-list {
    margin-left: 0;
    list-style: none;

    > li {
        margin-bottom: $spacing-unit;
    }
}

.post-meta {
    font-size: $small-font-size;
    color: $grey-color;
    transition: color 0.3s ease;

    [data-theme="dark"] & {
        color: $grey-color-dark-theme;
    }
}

.post-link {
    display: block;
    font-size: 24px;

    [data-theme="dark"] & {
        color: $text-color-dark;
    }
}

/**
 * Posts
 */
.post-header {
    margin-bottom: $spacing-unit;
}

.post-title {
    font-size: 42px;
    letter-spacing: -1px;
    line-height: 1;

    [data-theme="dark"] & {
        color: $text-color-dark;
    }

    @include media-query($on-laptop) {
        font-size: 36px;
    }
}

#disqus_thread {
    margin-top: 100px;
}

.post-content {
    margin-bottom: $spacing-unit;

    a.previous {
        margin-top: 30px;
        display: block;
        float: left;
    }

    a.next {
        margin-top: 30px;
        display: block;
        float: right;
    }

    h2 {
        font-size: 32px;

        @include media-query($on-laptop) {
            font-size: 28px;
        }
    }

    h3 {
        font-size: 26px;

        @include media-query($on-laptop) {
            font-size: 22px;
        }
    }

    h4 {
        font-size: 20px;

        @include media-query($on-laptop) {
            font-size: 18px;
        }
    }

    [data-theme="dark"] & {
        h2, h3, h4 {
            color: $text-color-dark;
        }
    }
}